body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-kanit: Kanit;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-sm: 14px;
  --font-size-base: 16px;

  /* Colors */
  --color-gray-100: #858585;
  --color-gray-200: #1c1c2a;
  --color-gray-300: #13131b;
  --color-white: #fff;
  --color-deepskyblue-100: #30b7f6;
  --color-deepskyblue-200: rgba(48, 183, 246, 0.25);
  --color-darkslateblue: #3b2e5f;

  /* Gaps */
  --gap-3xs: 10px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-xl: 20px;

  /* Border radiuses */
  --br-3xs: 10px;
}
